#top-tabbar-vertical {
    overflow: hidden;
    color: $body-color;
    margin: 0 -1.25rem 3.75rem;

    li {
        list-style-type: none;
        font-size: 1.125rem;
        width: 100%;
        float: left;
        position: relative;
        font-weight: 400;

        a {
            background: var(--#{$prefix}gray-900);
            // color: $dark;
            display: block;
            padding: 1.25rem 0.938rem;
            margin: 0.625rem 1.25rem;
            text-decoration: none;
            border: 1px solid rgba(var(--#{$variable-prefix}primary-rgb), 0.2);

            @if $enable-rounded ==true {
                @include radius-mixin($border-radius);
            }

            i {
                display: inline-block;
                text-align: center;
                height: 3.125rem;
                width: 3.125rem;
                line-height: 3.125rem;
                font-size: 1.25rem;
                margin: 0 1.25rem 0 0;

                @if $enable-rounded ==true {
                    @include radius-mixin($border-radius-pill);
                }
            }
        }

        &.active {
            a {
                border-color: transparent;

                i {
                    background: var(--#{$prefix}gray-900);
                }
            }

            &#personal {
                a {
                    background: rgba(var(--#{$variable-prefix}primary-rgb), 0.2);
                }
            }

            &#contact {
                a {
                    background: rgba($danger, 0.2);
                }
            }

            &#official {
                a {
                    background: rgba($success, 0.2);
                }
            }

            &#payment {
                a {
                    background: rgba($warning, 0.2);
                }
            }
        }
    }

    &.active {
        color: var(--#{$variable-prefix}primary);
    }
}

#form-wizard3 {
    fieldset {
        &:not(:first-of-type) {
            display: none;
        }
    }
}

.form-control[type="date"],
.form-control[type="month"],
.form-control[type="week"],
.form-control[type="time"],
.form-control[type="datetime-local"] {
    position: relative;

    &::-webkit-calendar-picker-indicator {
        position: absolute;
        right: .75rem;
        margin: 0;
    }
}

#top-tabbar-vertical li#personal i {
    background: #eff7f8;
    color: #089bab;
}

#top-tabbar-vertical li#contact i {
    background: #fff5f4;
    color: #f14336;
}

#top-tabbar-vertical li#official i {
    background: #eefff2;
    color: #27b345;
}

#top-tabbar-vertical li#payment i {
    background: #fffcf3;
    color: #fbc647;
}

#form-wizard3 {
    .form-control {
        // height: 45px;
        background-color: transparent;
        border: 1px solid #d7dbda;
        // font-size: 14px;
        // color: #a09e9e;
        border-radius: 10px;
    }

}

[dir="rtl"] {
    #top-tabbar-vertical li a i {
        margin: 0 0 0 1.25rem;
    }
}